.home{
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: rgb(127, 127, 218);

  > main{
    position: absolute;
display: flex;
width: 100%;
height: 100%;
flex-direction: column;
align-items: flex-end;
justify-content: center;

>div{
text-align: end;
  padding: 2rem;
  
}
  }

  &::before{
content: "";
width: 100%;
height: 100%;
position: absolute;
    background: no-repeat url("../assets/pexels-negative-space-34600.jpg");
    border-radius: 0 0px 90px 90px;
    filter: blur(1px);
  }
}
.home2{
  background-color: rgb(127, 127, 218);
  display: flex;
  flex-direction: row;
  justify-content:space-evenly;
  width: 100%;
  
  height: 100vh;
  align-items: center;

>img{
  width: 30%;
  object-fit: contain;
  animation: imgUp 0.7s linear infinite alternate;
}
p{
  padding: 0 10px;
}
  }
  @keyframes imgUp{
to{
  transform: translateY(-10px);
}
  }
.home3{
width: 100%;
height: 120vh;
background-color: rgb(127, 127, 218);
margin-top: -100px;

>.para{
width: 70%;
height: 100vh;
background-color: antiquewhite;
padding: 40px;
border-radius: 0 50px 50px 0;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}

}
.home4{
width: 100%;
height: 100vh;
display: flex;
background-color: rgb(127, 127, 218);
justify-content: flex-end;

>div{
  display: flex;
  flex-direction: column;
  justify-content: center;
align-items: center;
width: 70%;
background-color: blue;
border-radius: 50px 0 0px 50px;
height: 70vh;
padding: 2rem;
>h1{
  border-bottom: 2px solid white;
  color: white;
}
>article{
display: flex;
margin: 2rem 0;
justify-content: center;
flex-wrap: wrap;
>div{
  animation: imgUp 0.7s linear infinite alternate;
  width: 10rem;
  background-color: white;
  display: flex;
  flex-direction: column;
  border-radius: 50%;
  height: 10rem;
margin: 1rem;
align-items: center;
justify-content: center;
cursor: pointer;
transition: all 0.5s;
&:hover{
filter: invert(1);
}
svg{
font-size: 4rem;
}
p{
  font-size: 900 1.2rem white;
}
}
}
}
}
.home5{
width: 100%;
height: 100vh;
display: flex;
text-align: center;
justify-content: center;
align-items: center;
>main
{
  
  width:70%;
>form{
  width: 100%;
  border-radius: 50px;
  background-color: blue;
height: auto;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  h1{
    color: white;
    border-bottom: 2px solid white;
  }
  label, input{
    font-size: 18px;
    outline: none;
    padding: 2px;
    border-radius: 20px;
    
  }
  >button{
    margin: 10px 0;
    border-radius: 20px;
    font-size: 18px;
    padding: 4px 10px;
    outline: none;
   
    cursor: pointer;
    &:active{
      color: white;
      background-color: aquamarine;
    }
        }
}
}

}