nav{
    position: fixed;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    z-index: 10;
    opacity: 0.5;
    top: 0;
    background-color: rgb(226, 220, 220);
width: 100%;
    >h1{
        text-transform: uppercase;
    }
    >main{
        width: 70%;
        display: flex;
        justify-content: flex-end;
   
    > a {
        color: black;  
        margin: 1rem;
        text-decoration: none;
    }
    & :hover{
        color: red;
    }
    }
}
